.header {
  position: relative;
  padding: 65px 0 0;
  background: url("../img/logo-part.svg") no-repeat center 23px;
  z-index: 12;

  @media (min-width: @tablet-width) {
    padding: 0;
    background: none;
    display: flex;
    justify-content: flex-end;
  }

  @media (min-width: @desktop-width) {
    width: 1220px;
    margin: 0 auto;

    }
  }



.header__logo {
  position: absolute;
  top: 13px;
  left: 20px;

  @media (min-width: @tablet-width) {
    top: 25px;
    left: 30px;
  }

  @media (min-width: @desktop-width) {
    top: 55px;
    left: 0;
  }
}
